body {
  margin: 0;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav_list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav_item {
  margin-left: 20px;
}

.nav_item:first-child {
  margin-left: 0;
}

.nav_link {
  display: flex;
  align-items: center;
  color: black;
  text-decoration: none;
  text-transform: uppercase;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  height: 60px;
  position: relative;
  font-size: 12px;
}

.nav_link:hover {
  color: #00acdc;
}

.nav_link.is-active {
  color: #00acdc;
}

.nav_link.is-active::after {
  position: absolute;
  content: "";
  background-color: #00acdc;
  bottom: 0;
  left: 0;
  right: 0;
  height: 4px;
  border-radius: 8px;
}
